<template>
  <section>
    <draggable :list="dragtestlist" group="steps" @change="draggablechange"></draggable>
    <hr />
    <div>
      <div class="level">
        <div class="level-left">
          <div class="level-item">
            <b-button size="is-small" type="is-success" outlined @click="createNewFlowChart()">新建</b-button>
            <p class="pr-2 is-size-7">Test Run名称:</p>
            <b-input
              v-model="name"
              size="is-small"
              class="pr-2"
              placeholder="testrun名称"
              lazy
              style="width:260px"
            ></b-input>
            <div class="buttons">
              <b-button size="is-small" type="is-cblue-2" @click="saveFlowChart()">保存步骤</b-button>
              <b-button size="is-small" type="is-cred-2" @click="saveAll()">保存全部</b-button>
            </div>
          </div>
        </div>
        <div class="level-right">
          <div class="level-item">
            <b-button
              size="is-small"
              icon-left="arrow-right-drop-circle-outline"
              type="is-cgreen-4"
              @click="runDebug()"
            >
              执行(DEBUG)
            </b-button>
          </div>
        </div>
      </div>
    </div>
    <hr class="margin-05 has-background-cblue-2" />
    <div>
      <flow-chart
        ref="chart"
        width="1200"
        height="500"
        :nodes="nodes"
        :connections="connections"
        @editconnection="editConnection"
        @editnode="editNode"
        @save="handleSaveEvent"
      ></flow-chart>
    </div>
    <div>
      <div class="notification has-background-cgreen-4 margin-05 padding-05 ">
        <p>执行结果</p>
      </div>
      <b-table
        ref="table"
        :data="testrunresult"
        paginated
        per-page="5"
        detail-transition="fade"
        :show-detail-icon="false"
        aria-next-label="Next page"
        aria-previous-label="Previous page"
        aria-page-label="Page"
        aria-current-label="Current page"
        pagination-size="is-small"
        :narrowed="true"
      >
        <b-table-column v-slot="props" label="编号" width="400">
          <p class="is-size-7">{{ props.row.step.id }}</p>
        </b-table-column>

        <b-table-column v-slot="props" label="step名称" width="150">
          <a @click="openDetails(props.row)">
            <p class="is-size-7">{{ props.row.step.name }}</p>
          </a>
        </b-table-column>

        <b-table-column v-slot="props" label="METHOD" width="150">
          <p class="is-size-7">{{ props.row.step.request.method }}</p>
        </b-table-column>

        <b-table-column v-slot="props" label="验证类型" width="250">
          <p class="is-size-7">{{ props.row.logic.name }}</p>
        </b-table-column>

        <b-table-column v-slot="props" label="预期值" width="250">
          <p class="is-size-7">{{ props.row.result.expand }}</p>
        </b-table-column>

        <b-table-column v-slot="props" label="实际值" width="250">
          <p class="is-size-7">{{ props.row.result.actual }}</p>
        </b-table-column>

        <b-table-column v-slot="props" label="执行结果" width="100">
          <b-tag size="is-small" :type="returnResultColor(props.row.result.result)">
            {{ props.row.result.result }}
          </b-tag>
        </b-table-column>
      </b-table>
    </div>
    <div>
      <c-modal :is-open="shomodal.show" size="6xl">
        <c-modal-content ref="content">
          <c-modal-header>执行结果JSON</c-modal-header>
          <c-modal-body>
            <view-json :item="shomodal.item"></view-json>
          </c-modal-body>
          <c-modal-footer>
            <c-button @click="closeModal()">关闭</c-button>
          </c-modal-footer>
        </c-modal-content>
        <c-modal-overlay />
      </c-modal>
    </div>
  </section>
</template>

<script>
import { CModal, CModalOverlay, CModalContent, CModalHeader, CModalFooter, CModalBody, CButton } from '@chakra-ui/vue'
import draggable from 'vuedraggable'
import ViewJson from '@/components/Article/ViewJson'
import CheckLogic from '@/components/InterfaceRun/CheckLogic'
import FlowChart from 'flowchart-vue'
import { createGUID } from './../../scripts/internal'
import { initTestRun, executeWebTestRun, resultAssist } from '../../scripts/executeTestRunFunc'

export default {
  name: 'RunRightControl',
  components: {
    FlowChart,
    draggable,
    ViewJson,
    CModal,
    CModalOverlay,
    CModalContent,
    CModalHeader,
    CModalFooter,
    CModalBody,
    CButton
  },
  data() {
    return {
      /**
       * 整个testrun名称
       */
      name: '',
      /**
       * 图签节点
       */
      nodes: [],
      /**
       * 连接线
       */
      connections: [],
      /**
       * drag使用的展示list
       */
      dragtestlist: [],
      /**
       * 当前测试testrun
       */
      testrun: {},
      /**
       * 当前执行testrun
       */
      testrunresult: [],

      shomodal: {
        show: false,
        item: {}
      }
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    /**
     * 初始化
     */
    init() {
      this.nodes = [
        {
          id: 1,
          x: 20,
          y: 20,
          name: 'Start',
          type: 'start'
        },
        {
          id: 2,
          x: 600,
          y: 300,
          name: 'End',
          type: 'end'
        }
      ]
      this.connections = [
        /* {
                                  source: {
                                    id: 1,
                                    position: 'right'
                                  },
                                  destination: {
                                    id: 2,
                                    position: 'left'
                                  },
                                  id: 1,
                                  type: 'pass'
                                } */
      ]
      this.dragtestlist = []
      this.testrun = {}
      this.testrunresult = []
      this.name = ''
    },
    /**
     * 编辑连接线
     */
    editConnection(connection) {
      if (connection.type === 'pass') {
        connection.type = 'reject'
      } else {
        connection.type = 'pass'
      }
    },
    /**
     * 编辑node
     * NOTICE: 编辑主要是写入判定条件和预期值
     */
    editNode(node) {
      if (node.id !== 1 || node.id !== 2) {
        // 恢复默认值
        this.$buefy.modal.open({
          parent: this,
          component: CheckLogic,
          hasModalCard: true,
          trapFocus: true,
          CanCancel: false,
          props: {
            selectedlogic: {
              name: '',
              flag: -1,
              value: ''
            }
          },
          events: {
            updatelogic: value => {
              node.approvers[0].logic = value
            }
          }
        })
      }
    },
    /**
     * 保存执行图
     */
    handleSaveEvent(nodes, connections) {
      if (JSON.stringify(this.testrun) !== '{}') {
        // testrun非空则保存对应
        this.testrun.node = nodes
        this.testrun.connection = connections
        // 更新名称
        if (this.name.length !== 0 && this.testrun.name.length !== 0) {
          this.testrun.name = this.name
        }
      } else {
        // testrun空则新赋值保存对应
        const id = createGUID() // id是一个guid值
        if (this.name === '' || this.name === undefined || this.name === null) {
          // 无名自动复制
          this.name = id
        }
        this.testrun = {
          id: id,
          name: this.name,
          node: nodes,
          connection: connections
        }
      }
      // 弹窗
      this.$buefy.toast.open({
        duration: 1500,
        message: `执行流程保存/更新成功!`,
        position: 'is-top',
        type: 'is-success'
      })
      console.log(this.testrun)
      this.$store.commit('uploadTestRun', this.testrun) // 提交
    },

    /**
     * 拖拽draggable刷新
     */
    draggablechange(evt) {
      const step = evt.added.element
      const approver = {
        name: step.request.method + ':' + step.name,
        step: step,
        logic: {
          name: '验证Code等于',
          flag: 0,
          value: '200'
        }
      }
      this.$refs.chart.add({
        id: step.id,
        x: 300,
        y: 300,
        width: 220,
        height: 60,
        name: step.name,
        type: 'operation',
        approvers: [approver]
      })
    },
    /**
     * 点击保存按钮
     */
    saveFlowChart() {
      this.$refs.chart.save()
    },
    /**
     *点击创建按钮
     *一个新的执行流
     */
    createNewFlowChart() {
      this.saveFlowChart() // 保存(防止忘记保存)
      this.init() // 初始化
    },

    /**
     * 执行DEBUG
     */
    runDebug() {
      this.saveFlowChart() // 优先保存
      this.testrunresult = [] // 清空结果
      const tempitr = initTestRun(this.testrun) // 初始化测试运行,返回已经排序好的connection(从1->自己的步骤->2 结束)
      const run = executeWebTestRun(this.testrun.node, tempitr) // 定义yield方法
      const that = this
      for (let index = 0; index < tempitr.length; index++) {
        const result = run.next().value
        // console.log(result)
        if (result !== undefined) {
          result
            .then(res => {
              const resultb = resultAssist({
                step: res.step,
                logic: res.logic
              })
              // 替换或插入
              const qindex = this.testrunresult.findIndex(x => x.step.id === resultb.step.id)
              if (qindex === -1) {
                that.testrunresult.push(resultb)
              } else {
                this.testrunresult.splice(qindex, 1, resultb)
              }
            })
            .catch(err => {
              console.log(err)
            })
        }
        // console.log('下一个')
      }
      // console.log('执行结束')
    },

    /**
     * 保存全部
     */
    saveAll() {
      const tra = this.testrun
      tra.result = this.testrunresult
      this.$store.commit('uploadTestRunAll', tra) // 提交
    },

    /**
     * 打开详情
     * @param {Object} item 详情内容
     */
    openDetails(item) {
      // console.log(item)
      this.shomodal = {
        show: true,
        item: item
      }
    },
    closeModal() {
      this.shomodal = {
        show: false,
        item: {}
      }
    },

    /**
     * 返回执行颜色
     */
    returnResultColor(result) {
      if (result === true) {
        return 'is-success'
      } else {
        return 'is-danger'
      }
    },

    /**
     * 被父组件调用，直接赋值testrun
     */
    openTestRun(item) {
      // console.log('打开3')
      this.init()
      this.testrun = item
      this.name = item.name
      this.nodes = item.node
      this.connections = item.connection
    }
  }
}
</script>

<style scoped>
.margin-05 {
  margin: 0.5rem 0rem 0.5rem 0rem;
}

.padding-05 {
  padding: 0.5rem 0rem 0.5rem 1rem;
}
</style>
