<template>
  <section>
    <!--编辑主要是写入判定条件和预期值 -->
    <b-field label="预期条件" label-position="on-border">
      <b-select v-model="localselectedlogic" placeholder="判定条件">
        <option v-for="(option, index) in localchecklogic" :key="index" :value="option">{{ option.name }}</option>
      </b-select>
    </b-field>
    <b-field label="验证值" label-position="on-border">
      <b-input v-model="localselectedlogic.value" placeholder="验证值" lazy></b-input>
    </b-field>
    <b-button expanded size="is-small" @click="closeModal()">退出</b-button>
  </section>
</template>

<script>
import { CHECKLOGIC } from './../../scripts/internal'
export default {
  name: 'CheckLogic',
  props: {
    selectedlogic: Object
  },
  data() {
    return {
      localchecklogic: [],
      localselectedlogic: {}
    }
  },
  watch: {
    selectedlogic: {
      immediate: true,
      handler(val) {
        this.localselectedlogic = val
      },
      deep: true
    },
    localselectedlogic: {
      immediate: false,
      handler(val) {
        this.$emit('updatelogic', val)
      },
      deep: true
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    /**
     * 初始化
     */
    init() {
      // 初始化
      this.localchecklogic = CHECKLOGIC
    },
    closeModal() {
      this.$emit('close')
    }
  }
}
</script>

<style></style>
