<template>
  <div>
    <section class="has-background-cwhite-2 rebody">
      <p class="is-size-7">导入TestSuite进行TestRun的创建</p>
      <div class="file is-cblue-3" style="zoom:65%">
        <b-upload class="file-label" @input="ImportTestSuite">
          <span class="file-cta">
            <span class="file-label">导入 Test Suite</span>
          </span>
        </b-upload>
      </div>
      <b-field>
        <b-select v-model="selectcase" placeholder="选择suite/case" expanded size="is-small" @input="changeSelect">
          <optgroup v-for="(suiteitem, suiteindex) in localsuite" :key="suiteindex" :label="suiteitem.name">
            <option v-for="(caseitem, caseindex) in suiteitem.cases" :key="caseindex" :value="caseitem">
              {{ caseitem.name }}
            </option>
          </optgroup>
        </b-select>
      </b-field>
      <div>
        <draggable class="list-group" :list="selectsteps" :group="{ name: 'steps', pull: 'clone', put: false }">
          <b-message
            v-for="(item, index) in selectsteps"
            :key="index"
            type="is-cblue-2"
            class="padding-05 list-group-item"
          >
            {{ item.name }}
          </b-message>
        </draggable>
      </div>
    </section>

    <section class="has-background-cyellow-2 rebody">
      <p class="is-size-7">导入TestRun进行修改和执行测试</p>
      <div class="level">
        <div class="level-left">
          <div class="file is-cblue-1" style="zoom:70%">
            <b-upload class="file-label" @input="importTestRun">
              <span class="file-cta">
                <span class="file-label">导入全部testRun</span>
              </span>
            </b-upload>
          </div>
        </div>
        <div class="level-right">
          <b-button size="is-small" type="is-cyellow-1" @click="exportTestRun()">导出全部testRun</b-button>
        </div>
      </div>
      <div>
        <b-message
          v-for="(item, index) in localtestrun"
          :key="index"
          type="is-cgreen-4"
          class="padding-05 list-group-item"
          @dblclick.native="openTestRun(item)"
        >
          <p class="is-size-7 is-italic has-text-weight-bold">{{ item.name }}</p>
        </b-message>
      </div>
    </section>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
import { exportFileCommon } from '../../scripts/fileFunc'
export default {
  name: 'RunLeftControl',
  components: {
    draggable
  },
  props: {},
  data() {
    return {
      /**
       * 本地suites
       */
      localsuite: [],
      /**
       * 选择的case
       */
      selectcase: {},

      /**
       * 选择的step
       */
      selectsteps: [],

      localtestrun: []
    }
  },
  computed: {
    /**
     * 返回testsuite
     */
    getsuite() {
      return this.$store.state.testsuite.testsuite
    },

    gettestrun() {
      return this.$store.state.testrun.testrun
    }
  },
  watch: {
    getsuite: {
      immediate: true,
      handler(val) {
        this.localsuite = val
      },
      deep: true
    },
    gettestrun: {
      immediate: true,
      handler(val) {
        this.localtestrun = val
        // console.log(this.localtestrun)
      },
      deep: true
    }
  },
  methods: {
    /**
     * 导入testsuite
     */
    ImportTestSuite(file) {
      const that = this // 定义
      // 新建一个FileReader
      const reader = new FileReader()
      // 读取文件
      reader.readAsText(file, 'UTF-8')
      // 读取完文件之后会回来这里
      reader.onload = function(e) {
        // 读取文件内容(赋值)
        const importcontent = e.target.result
        // 接下来可对文件内容进行处理
        that.$store.commit('uploadTestSuite', importcontent) // 提交
        // that.$refs.suitent[0].updateTestSuite()
      }
    },
    /**
     * select更改事件
     */
    changeSelect(val) {
      this.selectsteps = val.steps
      // console.log(this.selectsteps)
    },
    /**
     * 导入testrun
     */
    importTestRun(file) {
      const that = this // 定义
      // 新建一个FileReader
      const reader = new FileReader()
      // 读取文件
      reader.readAsText(file, 'UTF-8')
      // 读取完文件之后会回来这里
      reader.onload = function(e) {
        // 读取文件内容(赋值)
        const importcontent = e.target.result
        // 接下来可对文件内容进行处理
        that.$store.commit('importTestRunCommon', importcontent) // 提交
      }
    },
    /**
     * 导出testrun
     */
    exportTestRun() {
      const testrun = this.$store.state.testrun.testrun
      testrun.forEach(sigtr => {
        sigtr.node.forEach(signode => {
          if ('approvers' in signode) {
            signode.approvers[0].step.response.body = ''
            signode.approvers[0].step.response.completeres = {}
          }
        })
      })
      const testrunstr = JSON.stringify(testrun)
      /**
       * testrunall导出不处理
       */
      const testrunall = this.$store.state.testrun.testrunall
      const testrunallstr = JSON.stringify(testrunall)
      exportFileCommon(testrunstr, 'testrun.json')
      exportFileCommon(testrunallstr, 'testrunall.json')
    },

    openTestRun(item) {
      // console.log('打开')
      this.$emit('set-test-run', item)
    }
  }
}
</script>

<style scoped>
.padding-075 >>> section {
  padding: 0.75rem;
}

.padding-05 >>> section {
  padding: 0.5rem;
}

.rebody {
  height: 400px;
  overflow: auto;
}
</style>
