<template>
  <section>
    <div class="columns">
      <div class="column is-2">
        <run-left-control @set-test-run="setTestRun"></run-left-control>
      </div>
      <div class="column">
        <run-right-control ref="runrightcontrol"></run-right-control>
        <!-- <run-right-control-demo></run-right-control-demo>-->
      </div>
    </div>
  </section>
</template>

<script>
import RunLeftControl from '@/components/InterfaceRun/RunLeftControl'
import RunRightControl from '@/components/InterfaceRun/RunRightControl'

export default {
  name: 'InterfaceRun',
  components: {
    RunLeftControl,
    RunRightControl
  },
  data() {
    return {}
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.testsuite = this.$store.state.testsuite.testsuite
    },
    setTestRun(item) {
      // console.log('打开2')
      this.$refs.runrightcontrol.openTestRun(item)
    }
  }
}
</script>

